body{
  background-color: black;
}

.App {
  text-align: center;
  display: flex;
  flex-direction: column;
}

.Text{
  color: aqua;
  font-size: 60px;
  font-weight: 700;
}

#ResultText{
  color: navy;
  font-size: 40px;
}

.ScoreText{
  color: royalblue;
  font-size: 30px;
}

.InputDiv{
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 60px;
  margin-top: 10px;
}

#GuessField{
  width: 30%;
  height: 100%;
  border: 2px solid grey;
  border-radius: 4px;
  margin: 2px;
}

.Button{
  border: 2px solid grey;
  border-radius: 8px;
  width: 160px;
  height: 50px;
}

@media only screen and (max-width: 900px) {
  #GuessField{
    width: 70%;
  }
  .Text{
    color: aqua;
    font-size: 40px;
    font-weight: 700;
  }
  
  #ResultText{
    font-size: 30px;
  }
  
  .ScoreText{
    color: royalblue;
    font-size: 20px;
  }
  .Button{
    width: 30%;
    height: 80px;
  }
}
